.container {
  max-width: 1024px;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
  letter-spacing: 0.1px;
  padding: 20px 30px 0;
}

@media (min-width: 1200px) {

  .container {
    padding: 0px;
  }
}

.content {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 30px 0;
}

.banner {
  font-size: 0.8em;
  padding: 5px 10px;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #61c482;
  text-align: center;
  color: white;
  font-weight: 600;
  letter-spacing: 0.2px;
  color: hsla(0,0%,100%,.90);
  width: 100%;
}

@media (min-width: 992px) {

  .banner {
    font-size: 1em;
  }
}

.banner .link {
  color: white;
  text-decoration: underline;
  display: inline-block;
  margin-left: 4px;
}


