.movie {
  background-color: #65DBC1;
  width: 146px;
  height: 210px;
  border-radius: 3px;
  display: inline-block;
  background-size: 100%;
  position: relative;
}

.youtubeContainer {
  -ms-flex: 1 1 100%;
      flex: 1 1 100%;
  margin-bottom: 50px;
  border-radius: 3px;
  width: 100%;

}

@media (min-width: 992px) {

  .youtubeContainer {
    width: auto;
    padding-right: 20px;
    -ms-flex: 1 1 400px;
        flex: 1 1 400px;
  }

  .youtubeContainer:nth-child(2n) {
    padding-right: 0;
  }
}

.youtube {
  background-color: #65DBC1;
  border-radius: 3px;
  display: inline-block;
  background-size: 100%;
  position: relative;
  width: 100%;
  min-height: 275px;
  background-position: 0;
}

.rating {
  opacity: 0;
  -webkit-transition: opacity 200ms ease-in;
  -o-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in;
  position: absolute;
  bottom: 0;
  font-weight: 600;
  color: white;
  right: 10px;
  pointer-events: none;
}

.coverOverlay {
  opacity: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background: rgba(23,23,23,0.4);
  -webkit-transition: opacity 200ms ease-in;
  -o-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in;
}

.coverOverlay:hover, .coverOverlay:hover + .rating{
  cursor: pointer;
  opacity: 1;
}

.movieContainer {
  display: inline-block;
  margin-left: -1px;
  margin-bottom: 25px;
  max-width: 146px;
  margin-right: 20px;
}

a.movieContainer:nth-child(2n){
  margin-right: 0px;
}

@media (min-width: 544px) {
  a.movieContainer:nth-child(2n){
    margin-right: 20px;
  }
}

@media (min-width: 992px) {
  a.movieContainer:nth-child(5n){
    margin-right: 0px;
  }
}

@media (min-width: 1200px) {
  a.movieContainer:nth-child(5n){
    margin-right: 38px;
  }
  a.movieContainer:nth-child(5n):nth-child(2n){
    margin-right: 20px;
  }
  a.movieContainer:nth-child(6n){
    margin-right: 0px;
  }
  a.movieContainer {
    margin-right: 38px;
  }
}

.movieName {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}



