.title {
  margin-bottom: 20px;
}

.container {
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -ms-flex-direction: column;
      flex-direction: column;
}

.container > .side {
  -ms-flex: 0 1;
      flex: 0 1;
  padding: 30px;
  overflow: hidden;
  min-width: 364px;
}

.container .description {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 30px;
  white-space: pre-line;
}

.backdrop {
  height: 100%;
  width: 100%;
  position: absolute;
  background-size: cover;
  opacity: 0.2;
  z-index: -1;
  border-radius: 3px;
}

.poster {
  border-radius: 3px;
  width: 100%;
}

@media (min-width: 544px) {
  .container.video {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .container {
    -ms-flex-direction: row;
        flex-direction: row;
  }
  .container > .side {
    overflow: inherit;
  }
  .container.video > .side {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 50%;
  }
  .container:not(.video) .description {
    padding-left: 0px; 
  }
  .poster {
    width: auto;
    height: 100%;
  }
  .videoThumbnail {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }
}

.error {
  color: #EB5242;
}



